.out {
  $this: &;

  display: flex;
  flex-direction: column;
  @include windowHeight();

  padding-top: $header-height;

  @include lg {
    padding-top: $header-height-lg;
    padding-bottom: rem(50); }

  &__header {
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;

    @include before {
      @include coverdiv;
      z-index: 9;

      background-color: $white;
      border: 1px solid  $c-border; }

    @include md {
      box-shadow: 0 0 10px rgba($black, .25);
      @include before {
        border: 0;
        border-bottom: 1px solid  $c-border; } } }

  &__main {
    flex-grow: 1; }

  &__side-controls {
    position: fixed;
    z-index: 15;
    top: 0;
    right: 0;
    bottom: 0;

    width: $container--lg-padding;

    @include lg {
      top: auto;
      width: 100%; } }

  &__aux-header {
    @extend #{$this}__header;
    box-shadow: 0px 2px 4px 0px rgba(43, 37, 35, 0.35);

    @include md {
      display: none; } }

  &--double-header {
    padding-top: $header-height * 2;

    @include lg {
      padding-top: $header-height-lg * 2; }

    @include md {
      padding-top: $header-height-lg; }

    #{$this}__header {
      top: $header-height;
      @include lg {
        top: $header-height-lg; }
      @include md {
        top: 0; } }

    #{$this}__aux-header {
      top: 0 !important; } } }


